'use client';

import { useState } from 'react';
import { supabase } from '@/utils/supabaseClient';
import { useRouter } from 'next/navigation';
import { Mail, Lock, Loader2, ChevronLeft } from 'lucide-react';

export default function Login() {
  const [email, setEmail] = useState('');
  const [senha, setSenha] = useState('');
  const [erro, setErro] = useState('');
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  async function handleLogin(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    setErro('');

    // 1. Faz login no Supabase Auth
    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password: senha
    });

    if (error || !data.user) {
      setErro('E-mail ou senha inválidos!');
      setLoading(false);
      return;
    }

    // 2. Busca o status de pagamento do usuário na tabela "usuarios"
    const { data: userData, error: userError } = await supabase
      .from('usuarios')
      .select('pagamento_confirmado')
      .eq('id', data.user.id)
      .single();

    if (userError) {
      setErro('Erro ao verificar status do pagamento.');
      setLoading(false);
      return;
    }

    if (!userData?.pagamento_confirmado) {
      setErro('Seu pagamento ainda não foi confirmado. Complete o pagamento para acessar o painel.');
      setLoading(false);
      return;
    }

    // 3. Libera o acesso ao painel
    router.push('/pages/painel');
  }

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-blue-50 to-blue-200 px-2">
      {/* Top Bar - Voltar */}
      <div className="w-full max-w-md mx-auto pt-10 pb-2">
        <button
          onClick={() => router.push('/')}
          className="flex items-center gap-2 text-blue-700 font-semibold hover:underline transition"
        >
          <ChevronLeft size={22} /> Voltar
        </button>
      </div>

      {/* Card de login */}
      <div className="bg-white rounded-2xl shadow-2xl p-10 w-full max-w-md relative">
        <div className="flex flex-col items-center mb-8">
          <img src="/logo-bgsolutions.png" alt="BG Solutions" className="mb-4 w-20 h-20 rounded-full shadow-md" />
          <h2 className="text-2xl font-bold text-blue-700 mb-1">Área do Cliente</h2>
          <p className="text-gray-600 mb-8 text-center">
            Acesse seu painel exclusivo de automação WhatsApp
          </p>
        </div>
        <form className="w-full flex flex-col gap-5" onSubmit={handleLogin}>
          <div className="relative">
            <Mail className="absolute left-3 top-3.5 text-blue-500" size={20} />
            <input
              type="email"
              placeholder="Seu e-mail"
              className="w-full pl-10 pr-4 py-3 border border-gray-200 rounded-lg shadow-sm focus:border-blue-600 focus:ring-1 focus:ring-blue-100 outline-none text-base transition"
              value={email}
              onChange={e => setEmail(e.target.value)}
              required
              disabled={loading}
            />
          </div>
          <div className="relative">
            <Lock className="absolute left-3 top-3.5 text-blue-500" size={20} />
            <input
              type="password"
              placeholder="Sua senha"
              className="w-full pl-10 pr-4 py-3 border border-gray-200 rounded-lg shadow-sm focus:border-blue-600 focus:ring-1 focus:ring-blue-100 outline-none text-base transition"
              value={senha}
              onChange={e => setSenha(e.target.value)}
              required
              disabled={loading}
            />
          </div>
          {erro && (
            <div className="bg-red-50 border border-red-300 text-red-700 p-3 rounded-lg text-center text-sm">
              {erro}
            </div>
          )}
          <button
            type="submit"
            className="w-full flex items-center justify-center gap-2 py-3 bg-blue-600 hover:bg-blue-700 transition text-white text-base font-bold rounded-lg shadow"
            disabled={loading}
          >
            {loading && <Loader2 size={20} className="animate-spin" />}
            {loading ? 'Entrando...' : 'Entrar'}
          </button>
        </form>
        <a href="/" className="text-blue-500 mt-6 hover:underline text-sm">Voltar para o site</a>
        <span className="mt-4 text-gray-400 text-xs text-center block">© 2025 BG Solutions</span>
      </div>
    </div>
  );
}
